import dayjs from 'dayjs';
import duration from 'dayjs/plugin/duration';
import {tripDate, tripTime} from '../utils';

export const createTripPointTemplate = (tripPoint) => {
  const {type, time, price, additionalOptions, destinationInfo, isFavorite} = tripPoint;

  const date = dayjs(time.start).format('MMM DD');
  const startTime = dayjs(time.start).format('HH:MM');
  const endTime = dayjs(time.end).format('HH:MM');
  dayjs.extend(duration);
  const durationEvent = dayjs.duration(dayjs(time.end).diff(dayjs(time.start))).format('HH MM');

  const favoriteClassName = isFavorite
    ? 'event__favorite-btn--active'
    : '';

  return `<li class="trip-events__item">
    <div class="event">
      <time class="event__date" datetime="2019-03-18">${tripDate(time.start)}</time>
      <div class="event__type">
        <img class="event__type-icon" width="42" height="42" src="img/icons/${type}.png" alt="Event type icon">
      </div>
      <h3 class="event__title">${`${type  } ${  destinationInfo.name}`}</h3>
      <div class="event__schedule">
        <p class="event__time">
          <time class="event__start-time" datetime="2019-03-18T10:30">${tripTime(time.start)}</time>
          &mdash;
          <time class="event__end-time" datetime="2019-03-18T11:00">${tripTime()}</time>
        </p>
        <p class="event__duration">${durationEvent}</p>
      </div>
      <p class="event__price">
        &euro;&nbsp;<span class="event__price-value">${price}</span>
      </p>
      <h4 class="visually-hidden">Offers:</h4>
      <ul class="event__selected-offers">
        <li class="event__offer">
          <span class="event__offer-title">${additionalOptions.option.type}</span>
          &plus;&euro;&nbsp;
          <span class="event__offer-price">${additionalOptions.option.price}</span>
        </li>
      </ul>
      <button class="event__favorite-btn ${favoriteClassName}" type="button">
        <span class="visually-hidden">Add to favorite</span>
        <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
          <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
        </svg>
      </button>
      <button class="event__rollup-btn" type="button">
        <span class="visually-hidden">Open event</span>
      </button>
    </div>
  </li>`;
};


